import React from "react";

// import './App.css';
class Showdetails extends React.Component {

    // Constructor 
    constructor(props) {
        super(props);

        this.state = {
            items: [],
            DataisLoaded: false
        };
    }

    componentDidMount() {
        fetch(
            "https://my-json-server.typicode.com/Ved-X/assignment/orders")
            .then((res) => res.json())
            .then((json) => {
                this.setState({
                    items: json,
                    DataisLoaded: true
                });
            })
    }
    render() {
        const { DataisLoaded, items } = this.state;
        if (!DataisLoaded) return <div>
            <h1 > Pleses wait some time.... </h1> </div>;

        return (
            <div className="details">

                <h1 className="allord">All orders <span className="allord"> {items.length}</span></h1>
             <div className="container">

           
                 <input id="form-input" type="text" placeholder="search name" />
                 
                 </div>
                <table className="styled-table">
                    <thead> <tr>
                        <th>ORDER ID</th>
                        <th>CUSTOMER</th>
                        <th>COUNTRY</th>
                        <th>ADDRESS</th>
                        <th>PRODUCT </th>
                        <th>Date Order</th>
                        <th>STATUS^</th>

                    </tr>
                    </thead>
                    <tbody>


                        {
                            items.map((details) => (
                                <tr>
                                    <td>{details.order_id}</td>
                                    <td>{details.customer}</td>
                                    <td>{details.country}</td>
                                    <td> {details.address}</td>
                                    <td>{details.product_title}</td>
                                    <td>{details.date}</td>
                                    <td > <span className={details.status}> {details.status}</span></td>

                                </tr>
                            ))
                        }
                    </tbody>
                </table>
            </div>
        );
    }
}

export default Showdetails;